$main-color: rgb(255, 255, 255);
$main-color2: #4070f4;
$main-color3: #707070;
$main-color4: #12171e;

@mixin display-flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
@mixin display-flex-column {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}
@mixin container {
  width: 100%;
  height: 100%;
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  scroll-behavior: smooth;
}
@mixin section {
  height: 100vh;
  width: 100vw;
  scroll-snap-align: start;
}
@mixin main-button {
  background-color: $main-color2;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  font-size: 24px;
  &:hover {
    background-color: $main-color3;
  }
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
body {
  background: $main-color;
  width: 100vw;
  height: 100vh;
}
.container {
  @include container();
}
section {
  @include section();
}
section:nth-child(1) {
  background-color: $main-color;
}
// sidebar start
.sidebar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 80px;
  background-color: $main-color4;
  padding: 0.4rem 0.8rem;
  transition: all 0.5s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.sidebar.active ~ .main-content {
  left: 250px;
  width: calc(100% - 250px);
}
.sidebar.active {
  width: 250px;
}
.sidebar #but {
  color: $main-color;
  position: absolute;
  top: 0.4rem;
  left: 50%;
  font-size: 1.2rem;
  line-height: 50px;
  transform: translateX(-50%);
  cursor: pointer;
}
.sidebar.active #but {
  left: 90%;
}
.sidebar .top .logo {
  color: $main-color;
  display: flex;
  height: 50px;
  width: 100%;
  align-items: center;
  pointer-events: none;
  opacity: 0;
}
.sidebar.active .top .logo {
  opacity: 1;
}
.top .logo i {
  font-size: 2rem;
  margin-right: 5px;
}
.sidebar ul li {
  position: relative;
  list-style-type: none;
  height: 50px;
  width: 90%;
  margin: 0.8rem auto;
  line-height: 50px;
}
.sidebar ul li a {
  color: $main-color;
  display: flex;
  align-items: center;
  text-decoration: none;
  border-radius: 0.8rem;
}
.sidebar ul li a:hover {
  background-color: $main-color;
  color: $main-color4;
}
.sidebar ul li a i {
  min-width: 50px;
  text-align: center;
  height: 50px;
  border-radius: 12px;
  line-height: 50px;
}
.sidebar .nav-item {
  opacity: 0;
}
.sidebar.active .nav-item {
  opacity: 1;
}
.sidebar ul li .tooltip {
  position: absolute;
  left: 125px;
  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0.5rem 0.8rem rgba(0, 0, 0, 0.2);
  border-radius: 6rem;
  padding: 0.4rem 1.2rem;
  line-height: 1.8rem;
  z-index: 20;
  opacity: 0;
  background-color: $main-color;
}
.sidebar ul li:hover .tooltip {
  opacity: 1;
}
.sidebar.active ul li .tooltip {
  display: none;
}
// sidebar end
.main-content {
  background: url(../scss/sonoma.jpg);
  background-repeat: no-repeat;
  background-size: auto 100vh;
  // background-color: $main-color3;
  position: relative;
  min-height: 100%;
  top: 0;
  left: 80px;
  transition: all 0.5s ease;
  width: calc(100% - 80px);
  padding: 1rem;
}

.frem {
  background-color: rgba(27, 27, 27, 0.9);
  backdrop-filter: blur(6px);
  height: 70vh;
  width: 70vw;
  border-radius: 10px;
  
}
.uper-bar {
  display: flex;
  align-items: center;
  background-color: rgb(57, 57, 57);
  height: 30px;
  border-radius: 10px 10px 0 0;
}

.cercel1,
.cercel2,
.cercel3 {
  height: 12px;
  width: 12px;
  margin: 4px;
  border-radius: 12px;
}
.cercel1 {
  background-color: #ff5f57;
  margin-left: 15px;
}
.cercel2 {
  background-color: #febc2e;
}
.cercel3 {
  background-color: #28c840;
}
.cercel1:hover,
.cercel2:hover,
.cercel3:hover {
  background-color: bisque;
}
.windo {
  color: aqua;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 14px;
}

.typing {
  color: antiquewhite;
  white-space: nowrap;
  overflow: hidden;
  width: 150px;
  border-right: 3px solid;
  animation: typing 1.5s steps(13), cursor 0.4s step-end infinite alternate;
}
@keyframes cursor {
  50% {
    border-color: transparent;
  }
}
@keyframes typing {
  from {
    width: 0px;
  }
}
.windo-rest {
  background: rgb(26, 142, 242);
}

section:nth-child(2) {
  background-color: $main-color2;
}
